@import url(https://fonts.googleapis.com/css2?family=Mulish:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mulish:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.searchEngine--container {
  width: 600px;
  height: 476px;
  background-color: #ffffff;
  border-radius: 20px;
  font-family: "Mulish", sans-serif;
  font-weight: bold;
  margin-top: 100px;
  margin-left: auto;
  margin-right: auto;
  box-shadow: 5px 5px 20px lightgrey;
}
.searchEngine--inner-container {
  width: 72%;
  display: inline-block;
  margin-left: 75px;
  margin-top: 70px;
  height: 70%;
}

.searchEngine--inner-container h3 {
  font-size: 14px;
  margin: 0px;
  margin-bottom: 7px;
}

.policy--title {
  font-size: 13px;
  width: 70%;
}

.policy--title span {
  color: blue;
  cursor: pointer;
}

.policy--title span:hover {
  color: #141e42;
}

.quill--button {
  width: 175px;
  height: 48px;
  background-color: #141e42;
  border: none;
  color: #ffffff;
  border-radius: 5px;
  font-size: 16px;
  font-family: inherit;
  margin: 15px 0px;
  cursor: pointer;
  transition: 0.3s ease;
}

.quill--button:hover {
  background-color: #283f93;
}

.result--container {
  width: 509px;
  height: 300px;
  background-color: #141e42;
  border-radius: 20px;
  margin: 100px auto;
  font-family: "Mulish", sans-serif;
  box-sizing: border-box;
  box-shadow: 5px 5px 10px lightgrey;
}

.result--container h5,
.result--container h3 {
  color: #ffffff;
  width: 60%;
}

.result--inner-container {
  width: 80%;
  height: 80%;
  margin-left: 45px;
  margin-top: 30px;
  display: inline-block;
}

.resultItem--container {
  background-color: #2b3455;
  display: inline-flex;
  color: #ffffff;
  margin: 2px 2px;
  align-items: center;
  padding: 10px 10px;
  border-radius: 5px;
  font-size: 14px;
  cursor: pointer;
  transition: 0.5s ease;
}

.resultItem--container:hover {
  background-color: #47568b;
}

